import React, { Component } from 'react';
import { Link } from 'react-router-dom';


import { makeStyles } from '@material-ui/core/styles';
import Slider from '@material-ui/core/Slider';
/* IMG */
import cohete from '../assets/img/cohetem.png';
import cohetepuf from '../assets/img/cohete.png';
import cohetebig from '../assets/img/cohetebig.png';

//CONECTAR COMPONENTE CON REDUX
import { connect } from 'react-redux';
import saveCalculadora from '../redux/actions'

const useStyles = makeStyles({
    root: {
      width: 300,
    },
  });
  
  const marks = [
    {
      value: 0,
      label: '$1,000',
    },
    {
      value: 100,
      label: '$8,000',
    },
  ];

  const dias = [
    {
      value: 0,
      label: '7',
    },
    {
      value: 100,
      label: '60',
    },
  ];
  

class Calculadora extends Component {

    constructor(props) {
        super(props);

        this.state = {
            value: 20,
            setValue: 1000,
            setDias:10,
            interes:41.064,
            tasadiaria:41.064 / 30,
            interesDiario:''
        }

        this.handleSliderChange = this.handleSliderChange.bind(this);
        this.handleSliderChangeDias = this.handleSliderChangeDias.bind(this);
        this.handleClick = this.handleClick.bind(this);
        this.valuetext = this.valuetext.bind(this);
        this.valueDias = this.valueDias.bind(this);
        this.valueLabelFormat = this.valueLabelFormat.bind(this);
    }

    valuetext = (value) =>{
        return `$ ${value}`;
    }
    valueDias = (value) =>{
        return `${value}`;
    }

    valueLabelFormat = (value) => {
      return marks.findIndex((mark) => mark.value === value);
    }
    valueLabelFormatDias = (value) => {
      return dias.findIndex((dia) => dia.value === value);
    }

    handleSliderChange = (event, newValue) => {
        this.setState({ setValue:newValue });
      };
    handleSliderChangeDias = (event, newValue) => {
        this.setState({ setDias: newValue });
        let interesTotal = this.state.setValue / this.state.setDias;
        this.setState({ interes: interesTotal });
      }
    
    handleClick = (values) => {
      let data = {
              recibiras: this.state.setValue,
              duracion: this.state.setDias,
              totalapagar: values
      }
      this.props.saveCalculadora(data);
    }


  render() {
    this.interesDiario = this.state.setValue/100 * this.state.tasadiaria;
    this.interesTotal = this.interesDiario *  this.state.setDias;
    this.ivainteres = this.interesTotal * 0.16;
    this.totalapagar =  this.state.setValue +   this.interesTotal + this.ivainteres;
    this.cattotal = (((this.totalapagar - this.state.setValue)/ this.state.setValue) * 12)*100;
    return (
        <div className="container_calculadora">
            <div className="section-calc-monto">
                <div className="slidecontainer">
                  <div className="row">
                  <div className="col-sm container_img_cohete">
                      { /*this.state.setValue > 2000 && this.state.setValue < 3500 ?
                          <img src={ cohetepuf } alt="prestify" className="cohete_img"></img>
                        : this.state.setValue > 3500 && this.state.setValue < 5000 ?
                          <img src={ cohete } alt="prestify" className="cohete_img"></img>
                        : this.state.setValue > 5000 && this.state.setValue < 6500 ?
                          <img src={ cohetepuf } alt="prestify" className="cohete_img"></img>
                        :
                          <img src={ cohete } alt="prestify" className="cohete_img"></img>*/
                      }
                    </div>
                    <div className="col-sm">
                    <p className="text-left parrafomini">Cunto es tu prstamo? </p>
                      <Slider
                          defaultValue={1000}
                          getAriaValueText={this.valuetext}
                          aria-labelledby="discrete-slider"
                          valueLabelDisplay="auto"
                          step={100}
                          marks
                          min={1000}
                          max={5000}
                          onChange={this.handleSliderChange}
                      />
                      <p className="texto-slide-men"></p>
                      <p className="text-left parrafomini">Duracin del prstamo </p>
                      <Slider
                          defaultValue={10}
                          aria-labelledby="discrete-slider"
                          getAriaValueText={this.valueLabelFormatDias}
                          step={1}
                          valueLabelDisplay="auto"
                          marks
                          min={7}
                          max={30}
                          onChange={this.handleSliderChangeDias}
                      />
                        <div className="text-center">
                            <Link to="/registro">
                              <p className="btn btn-prestify-main btn-lg text-center"  onClick={() => this.handleClick(this.totalapagar.toFixed(2))}> Solicita tu prstamo</p>
                            </Link>
                        </div>

                        <div className="calculos_container">
                          <div className="row">
                              <div className="col-8 font-weight-normal">Tasa Intres Mensual</div>
                              <div className="col-4 text-right font-weight-normal"> {this.state.interes.toFixed(2)} % </div>
                              <div className="col-8 font-weight-normal">Tasa Diaria</div>
                              <div className="col-4 text-right font-weight-normal"> {this.state.tasadiaria }  % </div>
                              <div className="col-8 font-weight-normal">Interes Diario</div>
                              <div className="col-4 text-right font-weight-normal"> $ { this.interesDiario.toFixed(2) }</div>
                              <div className="col-8 font-weight-normal">Interes Total</div>
                              <div className="col-4 text-right font-weight-normal">  $ {this.interesTotal.toFixed(2) } </div>
                              <div className="col-8 font-weight-normal">IVA Intereses</div>
                              <div className="col-4 text-right font-weight-normal">  $ { this.ivainteres.toFixed(2)} </div>
                              <div className="col-8 font-weight-normal">Total a pagar</div>
                              <div className="col-4 text-right font-weight-normal"> $ {this.totalapagar.toFixed(2) }</div> 
                              <div className="col-8 font-weight-normal">CAT(Costo Anual Total)</div>
                              <div className="col-4 text-right font-weight-normal"> { this.cattotal.toFixed(2)} % </div> 
                              <p className="font-weight-normal letras-chiquitas text-center"> Sin IVA, para fines informativos y de comparacin exclusivamente.<br/>* Montos y plazo mayores a $5,000 pesos disponibles slo para clientes Prestify. Aplican restricciones, ver <span>trminos y condiciones</span>. </p>
                          </div>

                      </div>

                      </div>
                    </div>
                </div>
            </div>
        </div>
    )
  }
}


//Propiedades que envia
const mapDispatchToProps = (dispatch) => {
  return {
    saveCalculadora: (data) => dispatch(saveCalculadora(data))
  };
}
export default connect(null,mapDispatchToProps)(Calculadora);
