import React, { Component } from 'react';
import { Redirect } from 'react-router-dom';
import { Link } from 'react-router-dom';

import '../styles/login.css'

import cohetepuf from '../assets/img/cohete.png';
import NavBar from '../components/NavBar';
import Footer from '../components/Footer';

/* Images */
import cohetelado from '../assets/img/icons/icon1.png';
import marciano from '../assets/img/icons/icon2.png';
import planeta from '../assets/img/icons/icon3.png';




class Login extends Component {
    constructor(props) {
        super(props);
        this.state = {
          username: '',
          password:'',
          issLogged:false,
          errorMessage:false,
        };
    
        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);

    }

    handleChange(event) {
        this.setState({value: event.target.value});
    }
    
    handleSubmit(event) {  
        event.preventDefault();
        if ( this.state.username === "admin@gmail.com" && this.state.password === "admin") {
            this.setState({ username:'' , password: '', issLogged: true });
        } else {
            this.setState({ username:'' , password: '', issLogged: false, errorMessage:true });
        }

    }

    renderMessage(){
        return (
            <div className="alert alert-danger mx-auto mt-03" role="alert">
                Usuario o contrasea incorrecta!
            </div>
        )
    }


    render() {

        return (
        <div>
            <NavBar location={this.props.history} />
                <div className="container-fluid container-main container-inside">
                    <section id="login">
                        <div className="container">
                        <div className="row">
                            <div className="col-sm padding-loggin">
                            <h3 className="title-bold-h">Dinero</h3>
                            <h3 className="title-semibild">Rapido y Fcil</h3>
                            <p className="parrafo-semibild">Pgalo sin salir de casa!</p>
                            <div className="container-parrafo-completo">
                                <p className="parrafo"><span className="img_bullet"><img src={ cohetelado } alt="Prestify" className="icon_bullet"/></span><span className="texto_bullet"> Solucin fcil y rpida para conseguir dinero.</span></p>
                                <p className="parrafo"><span className="img_bullet"><img src={ marciano } alt="Prestify" className="icon_bullet"/></span><span className="texto_bullet"> Recibe el dinero en pocos minutos.</span></p>
                                <p className="parrafo"><span className="img_bullet"><img src={ planeta } alt="Prestify" className="icon_bullet"/></span><span className="texto_bullet"> Necesitars: Tu INE, comprobante de ingresos y cuenta bancaria a tu nombre.</span></p>
                            </div>
                            </div>
                            <div className="col-sm padding-loggin-b">
                            <h3 className="title-bold-h text-center padding-5">Inicia Sesin</h3>
                                <form onSubmit={ this.handleSubmit }>
                                    <div className="form-group">
                                        <label for="email">Correo electrnico</label>
                                        <input 
                                            type="email" 
                                            className="color-form-contacto" 
                                            placeholder="name@example.com"
                                            value= { this.state.username }
                                            onChange= { (e) => { this.setState({ username: e.target.value })}}
                                        />
                                    </div>
                                    <div className="form-group">
                                        <label for="password">Password</label>
                                        <input 
                                            type="password" 
                                            className="color-form-contacto" 
                                            placeholder="*******************"
                                            value= { this.state.password }
                                            onChange= { (e) => { this.setState({ password: e.target.value })}}
                                        />
                                    </div>
                                    <div className="form-group">
                                        <p>Si no recuerdas tu contrasea da <Link to="/resetpassword" className="link-to">clic aqu</Link></p>
                                    </div>
                                    <div className="form-group">
                                        <input type="submit" value="INGRESAR"  className="send_button" />
                                    </div>
                                </form>
                            </div>
                        </div>
                        </div>
                </section>
                </div>
          <Footer location={this.props.history} />
          </div>
        );
      }
  }

export default Login;
