import React, { Component } from 'react';
import { Redirect } from 'react-router-dom';
import Footer from '../components/Footer';
import NavBar from '../components/NavBar';
/* Images */
import cohetelado from '../assets/img/icons/icon1.png';
import '../styles/login.css'



class ResetPassword extends Component {
    constructor(props) {
        super(props);
        this.state = {
          username: '',
          password:'',
          issLogged:false,
          errorMessage:false,
        };
    
        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);

    }

    handleChange(event) {
        this.setState({value: event.target.value});
    }
    
    handleSubmit(event) {  
        event.preventDefault();
        if ( this.state.username === "admin@gmail.com" && this.state.password === "admin123") {
            this.setState({ username:'' , password: '', issLogged: true });
        } else {
            this.setState({ username:'' , password: '', issLogged: false, errorMessage:true });
        }

    }

    renderMessage(){
        return (
            <div className="alert alert-danger mx-auto mt-03" role="alert">
                Usuario o contrasea incorrecta!
            </div>
        )
    }


    render() {

        return (
        <div>
            <NavBar location={this.props.history} />
          <div className="container-fluid container-main container-inside">
              <div className="row">
                <div className="col container_thank">
                    <h3 className="text-center titles-gracias">Recupera tu contrasea!</h3>
                    <p className="text-center">Revisa la bandeja de tu correo electrnico <br></br> para restablecer tu contrasea.</p>
                    <div className="container col-md-reset"> 
                            <form onSubmit={ this.handleSubmit }>
                                <div className="form-group">
                                    <label for="email">Correo electrnico</label>
                                    <input 
                                        type="email" 
                                        className="color-form-contacto" 
                                        placeholder="name@example.com"
                                        value= { this.state.username }
                                        onChange= { (e) => { this.setState({ username: e.target.value })}}
                                    />
                                </div>
                                <div className="form-group">
                                    <input type="submit" value="INGRESAR"  className="send_button" />
                                </div>
                            </form>
                    </div> 
                </div>
            </div>
          </div>
            <Footer location={this.props.history} />
        </div>
        );
      }
  }

export default ResetPassword;
