import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import logo from '../assets/img/logo.png'
import $ from 'jquery';

class NavBar extends Component {

  constructor(props) {
    super(props); 
    this.state = {
       location:'/'
    }
  
  }

  render() {
    let { pathname } = this.props.location.location;
    window.onload = function(e) {
      $(window).scroll(function(e) {
        $('#navbar').each(function() {
        var imagePos = $(this).offset().top;
        var topOfWindow = $(window).scrollTop();
          if (imagePos >  100 ) {
            $(this).addClass('bggpurple');
          }
          else {
            $(this).removeClass('bggpurple');
          }
        });
      });
    }
    return (
      <div>
          <nav id="navbar" className={pathname === '/' ? "navbar navbar-fixed-top navbar-expand-lg navbar-light navbar-fixed-top" : pathname === '/home' ? "navbar navbar-fixed-top navbar-expand-lg navbar-light navbar-fixed-top bggpurple" : "navbar navbar-fixed-top navbar-expand-lg navbar-light navbar-fixed-top bggray"}>
            <Link to="/" className="navbar-brand" href="#"><img src={ logo } alt="prestify" className="logo-principal"></img></Link>
            <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNavDropdown" aria-controls="navbarNavDropdown" aria-expanded="false" aria-label="Toggle navigation">
                <span className="navbar-toggler-icon"></span>
            </button>
            <div className="collapse navbar-collapse navbar-right" id="navbarNavDropdown">
                <ul className="navbar-nav">
                <li className="nav-item active">
                    <a href="/registro" className="nav-link" target="_self">Prstamos personales <span className="sr-only">(current)</span></a>
                </li>
                <li className="nav-item">
                    <a href="/preguntas" className="nav-link" target="_self">Preguntas</a>
                </li>
                <li className="nav-item">
                    <a href="/contacto" className="nav-link" target="_self">Contacto</a>
                </li>
                <li className="nav-item">
                    <a href="/login" className="nav-link" target="_self" >Mi cuenta</a>
                </li>
                </ul>
            </div>
            </nav>
          </div>
    )
  }
}

export default NavBar;
